.form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .label {
    font-size: 1.2em;
    margin-bottom: 15px;
    color: #34495e;
  }
  
  .input {
    width: 100%;
    max-width: 200px;
    padding: 10px;
    font-size: 1em;
    border: 2px solid #bdc3c7;
    border-radius: 4px;
    text-align: center;
  }
  
  .input:focus {
    outline: none;
    border-color: #3498db;
  }
  
  .button {
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 1em;
    color: white;
    background-color: #2980b9;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .button:hover {
    background-color: #3498db;
  }
  
  .button:active {
    transform: translateY(1px);
  }