.form {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  .formGroup {
    display: flex;
    flex-direction: column;
  }
  
  .label {
    margin-bottom: 5px;
    font-weight: bold;
    color: #34495e;
  }
  
  .input {
    padding: 10px;
    border: 1px solid #bdc3c7;
    border-radius: 4px;
    font-size: 1em;
  }
  
  .input:focus {
    outline: none;
    border-color: #3498db;
  }
  
  .checkbox {
    margin-right: 10px;
  }
  
  .suggestions {
    list-style-type: none;
    padding: 0;
    margin: 5px 0;
    border: 1px solid #bdc3c7;
    border-radius: 4px;
    max-height: 150px;
    overflow-y: auto;
  }
  
  .suggestionItem {
    padding: 10px;
    cursor: pointer;
  }
  
  .suggestionItem:hover {
    background-color: #ecf0f1;
  }
  
  .error {
    color: #e74c3c;
    font-size: 0.9em;
    margin-top: 5px;
  }
  
  .button {
    align-self: flex-start;
    padding: 10px 20px;
    font-size: 1em;
    color: white;
    background-color: #27ae60;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .button:hover {
    background-color: #2ecc71;
  }
  
  .button:active {
    transform: translateY(1px);
  }